
import {
  defineComponent,
  ref
} from 'vue'

export default defineComponent({
  setup () {
    // 变量
    const active = ref(false)

    // 按钮点击效果
    const test = (): void => {
      active.value = true
    }
    return {
      // 变量
      active,
      // 方法
      test
    }
  }
})
